// Price filter widget
.widget_price_filter {

	margin-bottom: 40px;

	//.widget-title {

	//}

	.price_slider {
		margin-bottom: 1em;
	}

	.price_slider_amount {
		text-align: left;
		line-height: 2.4;
		font-size: 0.8751em;

		.button {
			font-size: 1.15em;
			float: right;
		}

		.price_label {

			.from,
			.to {
				font-weight: 700;
			}
		}
	}

	.ui-slider {
		position: relative;
		text-align: left;
		margin-left: 0.5em;
		margin-right: 0.5em;
	}

	.ui-slider .ui-slider-handle {
		position: absolute;
		z-index: 2;
		width: 0.6em;
		height: 0.6em;
		background-color: theme-color("light");
		border-radius: 0;
		cursor: ew-resize;
		outline: none;
		top: -0.15em;
		/* rtl:ignore */
		margin-left: -0.5em;
	}

	.ui-slider .ui-slider-range {
		position: absolute;
		z-index: 1;
		font-size: 0.7em;
		display: block;
		border: 0;
		border-radius: 1em;
		background-color: theme-color("light");
	}

	.price_slider_wrapper .ui-widget-content {
		border-radius: 1em;
		background-color: #C0C0C0;
		border: 0;
	}

	.ui-slider-horizontal {
		height: 0.3em;
	}

	.ui-slider-horizontal .ui-slider-range {
		top: 0;
		height: 100%;
	}

	.ui-slider-horizontal .ui-slider-range-min {
		left: -1px;
	}

	.ui-slider-horizontal .ui-slider-range-max {
		right: -1px;
	}
}
